<br><span class="rvts73">※备注：</span><span class="rvts54">除了</span><span class="rvts72">复活（</span><span class="rvts74">Resurrect</span><span class="rvts72">）卷轴和诅咒卷轴（</span><span class="rvts74">Curse Scroll</span><span class="rvts72">）</span><span class="rvts54">外，所有的卷轴都可以通过与其对应派系的空白技能书，合成相应的魔法技能书。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



